import { ServiceAllService } from './../../../services/service-all.service';
import { UtilService } from './../../../services/util.service';
import { Router, ActivatedRoute } from '@angular/router';
import { ProductService } from './../../../services/product.service';
import { Product } from '../../../models/produtos/product.model';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-product-delete',
  templateUrl: './product-delete.component.html',
  styleUrls: ['./product-delete.component.css']
})
export class ProductDeleteComponent implements OnInit {

  product : Product = {
    Id: null,
    DtCriacao : null,
    Nome: null,
    Preco: null,
    CriadoPor : null,
    Bloqueado : null,
    EmpresaId :null
  }

  constructor(private productService : ProductService,
              private router : Router,
              private utilService : UtilService,
              private route : ActivatedRoute,
              private deleteProduto : ServiceAllService<Product> ) { }

  ngOnInit(): void {
    
    const id = this.route.snapshot.paramMap.get('Id')
    this.productService.readById(id).subscribe(product => {
      this.product = product;
    });

  }
  deleteProduct():void{
      const tipo = `${"/Produto"}`
    //this.productService.delete(this.product.id).subscribe(() => {
      this.deleteProduto.delete(this.product.Id, tipo).subscribe(() => {
        this.utilService.showMessage("Produto Excludo com Sucesso!")
        this.router.navigate(['/products'])
      })
    
  }

  cancel():void{
    this.router.navigate(['/products'])
  }

}


  

 



  
